<template>
  <section class="card">
    <div class="card-header">
      <div class="utils__title">
        <strong>Create Expenditure</strong>
      </div>
    </div>
    <div class="card-body">
      <div :class="$style.addPost">
        <a-form class="mt-3" :form="form" @submit="handleSubmit">
          <div class="form-group">
            <a-form-item label="Particulars">
              <a-input v-decorator="['particulars',{ rules: [{ required: true, message: 'Please give a particular' }]}]" placeholder="Particulars"/>
            </a-form-item>
            <a-form-item label="Amount">
              <a-input-number
                :initialValue="0"
                v-decorator="['amount',{ rules: [{ required: true, message: 'Please provide an amount' }]}]"
                placeholder="Amount"
                style="width: 100%"
              />
            </a-form-item>
            <a-form-item label="Date">
              <a-date-picker @change="onChange" v-decorator="['date',{ rules: [{ required: true, message: 'Please provide date' }]}]" />
            </a-form-item>
          </div>
          <div class="form-group">
            <a-form-item>
              <div :class="$style.submit">
                <span class="mr-3">
                  <a-button type="primary" htmlType="submit">Submit and Save</a-button>
                </span>
                <a-button @click="handleReset" type="danger">Discard</a-button>
              </div>
            </a-form-item>
          </div>
        </a-form>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: 'index',
  data() {
    return {
      form: this.$form.createForm(this),
    }
  },
  methods: {
    handleReset() {
      this.form.resetFields()
    },
    onChange(date, dateString) {
      const form = this.form
      this.form.setFieldsValue({
        date: dateString,
      })
    },
    handleSubmit(e) {
      e.preventDefault()
      this.form.validateFields((err, values) => {
        if (!err) {
          this.$nprogress.start()

          const dateValues = {
            ...values,
            'date_picker': values['date'].format('YYYY-MM-DD'),
          }

          const requestPayload = {
            particulars: dateValues.particulars,
            amount: dateValues.amount,
            purchase_date: dateValues.date_picker,
          }

          this.axios.post('expenditure/store', requestPayload)
            .then(response => {
              this.$nprogress.done()
              // console.log('stats', response)
              if (response.status === 200) {
                this.form.resetFields()
                this.$notification['success']({
                  message: 'Expenditure Creation',
                  description: 'You have successfully Created an expenditure',
                })
              }
            })
            .catch(error => {
              // console.log('error', error.response.data.data)
              if (error.response.status === 401) {
                this.$notification['warning']({
                  message: 'Expenditure Creation',
                  description: 'Sorry you have a validation error ',
                })
              } else {
                this.$notification['warning']({
                  message: 'Expenditure Creation',
                  description: 'Sorry an error occurred.',
                })
              }
            })
        }
      })
    },
  },
}
</script>

<style lang="scss" module>
  @import "../issues/style.module.scss";
</style>
